function _defineProperty(obj, key, value) { if (key in obj) { Object.defineProperty(obj, key, { value: value, enumerable: true, configurable: true, writable: true }); } else { obj[key] = value; } return obj; }

import { useConfig } from '@dhis2/app-runtime';

class Api {
  constructor({
    baseUrl
  }) {
    _defineProperty(this, "request", ({
      url,
      method,
      body
    }) => fetch(`${this.baseUrl}/api/${url}`, {
      method,
      body,
      credentials: 'include'
    }).then(res => {
      if (res.status < 200 || res.status >= 300) {
        throw new Error(res.statusText);
      }

      return res;
    }));

    _defineProperty(this, "installVersion", (versionId, userGroupId) => {
      let url = `appHub/${versionId}`;

      if (userGroupId) {
        url += `?group=${userGroupId}`;
      }

      this.request({
        url,
        method: 'post'
      });
    });

    _defineProperty(this, "uninstallApp", appKey => this.request({
      url: `apps/${appKey}`,
      method: 'delete'
    }));

    _defineProperty(this, "uploadApp", file => {
      const data = new FormData();
      data.append('file', file);
      return this.request({
        url: 'apps',
        method: 'post',
        body: data
      });
    });

    this.baseUrl = baseUrl;
  }

}

export const useApi = () => {
  const {
    baseUrl
  } = useConfig();
  return new Api({
    baseUrl
  });
};